/**
 * BinaryString, UintXXArray, ArrayBuffer Blob
 * 
 * @memberof tatLogDiver.Graph
 * @param {Object}
 *            arrayBuffer 
 * @return {Blob} 
 */
Graph.prototype.menuBuffToBlob = function (arrayBuffer) {
    return new Blob([ arrayBuffer ], {
        type : "application/octet-stream"
    });
};

/**
 * blob
 * 
 * @memberof tatLogDiver.Graph
 * @param {Objcet}
 *            blob blog
 * @param {String}
 *            menuId Chrome, FireFoxHTML
 * @param {String}
 *            fileName ie11
 */
Graph.prototype.menuDownloadBlob = function (blob, menuId, fileName) {
    if (window.navigator.msSaveBlob) { // ie11
        window.navigator.msSaveBlob(blob, fileName);
        // msSaveOrOpenBlob
        window.navigator.msSaveOrOpenBlob(blob, fileName);
    } else { // Chrome, FireFox
        document.getElementById(menuId).href = window.URL.createObjectURL(blob);
    }
};

